.popupTrailer {
	display: flex;
	width: 100vw;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	align-items: center;
	justify-content: center;
	background: rgba(0, 0, 0, 0.6);
	visibility: hidden;
	z-index: 2;
	border-radius: 15px;
}

.popupTrailer_opened {
	opacity: 1;
	visibility: visible;
}

.popupTrailer__close {
	position: absolute;
	width: 25px;
	height: 25px;
	top: 165px;
	left: 1190px;
	background-image: url(../../images/cross.svg);
	background-color: transparent;
	background-size: cover;
	align-self: flex-start;
	padding: 0;
	margin: 0;
	border: none;
}
